'use server'

import { Resend } from 'resend'

import { render } from '@react-email/render'
import { BookingNotificationEmail } from '@/emails/new-booking-email'

const resend = new Resend(process.env.RESEND_API_KEY)

interface BookingEmailData {
  name: string
  phone_number: string
  package_title: string
  date: string
  guests: number
  adults: number
  children: number
  total_price: number
  status: string
  notes?: string
}

export async function sendBookingEmail(booking: BookingEmailData) {
  console.log("DEBUG: Starting email process...")
  console.log("DEBUG: Checking API Key:", process.env.RESEND_API_KEY ? "EXISTS" : "MISSING")
  console.log("DEBUG: Payload prepared", JSON.stringify(booking, null, 2))

  if (!process.env.RESEND_API_KEY) {
    console.error("DEBUG: RESEND_API_KEY is missing in process.env!")
    return { success: false, error: "Missing API Key" }
  }

  // 1. Try sending with React Email Template
  try {
    // Manually render (Debug mode)
    console.log("DEBUG: Attempting to render React component...")
    const html = await renderAsync(
      BookingNotificationEmail({
        name: booking.name,
        phone_number: booking.phone_number,
        package_title: booking.package_title,
        date: booking.date,
        guests: booking.guests ?? 1,
        adults: booking.adults ?? 1,
        children: booking.children ?? 0,
        total_price: booking.total_price ?? 0,
        status: booking.status ?? 'Pending',
        notes: booking.notes
      })
    )
    console.log("DEBUG: Render successful. Sending email...")

    const { data, error } = await resend.emails.send({
      from: 'Marragafay Bookings <onboarding@resend.dev>',
      to: ['marragafay@gmail.com'],
      subject: `New Booking: ${booking.name} - ${booking.package_title}`,
      html: html
    })

    if (error) {
      console.error('Resend API send failed (after render):', error)
      throw error // Throw to trigger fallback
    }

    return { success: true, data }
  } catch (reactError) {
    console.error('Failed to RENDER or SEND React email, attempting fallback HTML...', reactError)
    // Fallback logic follows...
    console.error('Failed to send React email, attempting fallback HTML...', reactError)

    // 2. Fallback to basic HTML if React rendering failed
    try {
      const { data, error } = await resend.emails.send({
        from: 'Marragafay Bookings <onboarding@resend.dev>',
        to: ['marragafay@gmail.com'],
        subject: `New Booking: ${booking.name} - ${booking.package_title} (Fallback)`,
        html: `
          <h1>New Booking Received</h1>
          <p><strong>Customer:</strong> ${booking.name}</p>
          <p><strong>Phone:</strong> ${booking.phone_number}</p>
          <p><strong>Package:</strong> ${booking.package_title}</p>
          <p><strong>Total Price:</strong> ${booking.total_price} MAD</p>
          <p><strong>Status:</strong> ${booking.status}</p>
          <p style="color: red; font-size: 12px;">Note: The fancy email template failed to render. Please check server logs.</p>
        `
      })

      if (error) {
        console.error('Fallback HTML email also failed:', error)
        return { success: false, error }
      }

      return { success: true, data }
    } catch (htmlError) {
      console.error('CRITICAL: All email attempts failed:', htmlError)
      return { success: false, error: htmlError }
    }
  }
}
